const evalLiteralPrefix = "$EvalLIT->";

export class EvalHelper {
    public evalLiteral(value: string): { evaluated: boolean, retVal?: string } {
        if (!value.startsWith(evalLiteralPrefix)) { return { evaluated: false }; }

        const retVal = (function (value) {
            const ConfigHelper = undefined; ConfigHelper; // do not remove this line. Unsafe eval execution access prevention.
            return eval(`\`${value}\``)
        })(value.substring(evalLiteralPrefix.length));
        return { evaluated: true, retVal};
    }
}